.product {
  background-color: var(--color-primary-900);
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  display: flex;
}

.product h1 {
  text-align: center;
  margin-bottom: 6px;
  font-size: 24px;
}

@media only screen and (width >= 1024px) {
  .product h1 {
    font-size: 32px;
  }
}

.product h2 {
  font-size: 20px;
}

@media only screen and (width >= 1024px) {
  .product h2 {
    font-size: 24px;
  }
}

.product-content {
  flex: 1;
  padding: 20px 12px;
}

@media only screen and (width >= 600px) {
  .product-content {
    padding-left: 24px;
    padding-right: 24px;
  }
}

@media only screen and (width >= 1024px) {
  .product-content {
    padding-left: 48px;
    padding-right: 48px;
  }
}

@media only screen and (width >= 1440px) {
  .product-content {
    max-width: 1300px;
    margin: 0 auto;
  }
}

.product-content-container {
  background-color: var(--color-primary-700);
  border-radius: 24px;
  flex-direction: column;
  align-items: center;
  gap: 24px;
  padding: 12px;
  display: flex;
}

@media only screen and (width >= 600px) {
  .product-content-container {
    padding: 24px;
  }
}

@media only screen and (width >= 1280px) {
  .product-content-container {
    flex-direction: row;
    align-items: stretch;
  }
}

.product-content-container .product-info {
  flex-direction: column;
  gap: 24px;
  width: 100%;
  display: flex;
}

.product-content-container .product-info-header {
  background-color: var(--color-primary-900);
  border-radius: 24px;
  flex-direction: column;
  align-items: center;
  padding: 24px;
  display: flex;
}

.product-content-container .product-info-description {
  white-space: pre-wrap;
  margin-bottom: 16px;
  margin-left: 18px;
  margin-right: 18px;
  font-size: 20px;
}

.product-content-container .product-info-cart-buttons {
  flex-direction: column;
  gap: 16px;
  margin-top: auto;
  display: flex;
}

.product-content-container .product-info-cart-buttons .added-text {
  color: var(--color-success-100);
  justify-content: center;
  align-items: center;
  gap: 8px;
  height: 40px;
  font-size: 16px;
  display: flex;
}

.product-content-container .product-info-cart-buttons .button {
  color: #fff;
  cursor: pointer;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  gap: 6px;
  height: 40px;
  font-size: 16px;
  display: flex;
  box-shadow: 4px 4px 20px #000;
}

.product-content-container .product-info-cart-buttons .button.add {
  background-color: var(--color-primary-500);
  border: 1px solid var(--color-primary-200);
}

.product-content-container .product-info-cart-buttons .button.go-to-cart {
  background-color: var(--color-primary-600);
  border: 1px solid var(--color-primary-500);
}

.product-content-container .product-info-cart-buttons .button.continue-shopping {
  background-color: var(--color-primary-500);
  border: 1px solid var(--color-primary-200);
}

.product-content-container .product-info-cart-buttons .button.remove {
  background-color: var(--color-error-30);
  border: 1px solid var(--color-error-30);
  gap: 2px;
}
